import * as React from "react";
import Navigation, { NavigationItem } from "src/components/Navigation/Navigation";
import Banner from "src/components/Banner";
import styled from "styled-components";

export interface UpfitProps {}

const Content = styled.div`
    width: 100%;
    padding-bottom: 200px;
    font-size: 14px;
`;

const Desc = styled.div`
    padding: 0 15%;
    & > div {
        display: flex;
        margin-bottom: 60px;
        & > div:nth-child(1) {
            width: calc(20% - 45px);
            margin-right: 45px;
            font-size: 18px;
        }
        & > div:nth-child(2) {
            width: 80%;
            line-height: 1.71;
            font-size: 14px;

            & > p:nth-child(1) {
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: normal;

                &.title {
                    font-size: 32px;
                    font-weight: bold;

                    margin-bottom: 34px;
                }

                &.subTitle {
                    font-size: 22px;
                    font-weight: bold;

                    margin-bottom: 10px;
                }
            }
        }
    }
`;

export default class Upfit extends React.Component<UpfitProps, any> {
    public render() {
        return (
            <>
                <Navigation list={[{ name: "홈", link: "/" }, { name: "브랜드", link: "/brand/" }, { name: "업핏", link: "/upfit" }]}>
                    <NavigationItem link="http://www.upfit.co.kr">upfit 홈페이지 바로가기</NavigationItem>
                </Navigation>
                <Content>
                    <Banner>
                        <img src={require("../../assets/brand/upfit/upfit-banner.png")} alt="" />
                        <div className="desc typing">
                            <h1 lang="en" style={{ color: "white" }}>
                                upfit
                            </h1>
                            <h2 style={{ color: "white" }}>UPDATE YOUR FIT UPGRADE YOUR LIFE</h2>
                        </div>
                    </Banner>

                    <Desc>
                        <div>
                            <div />
                            <div>
                                <p className="title">Update Your Fit, Upgrade your life</p>
                                <p>
                                    우리가 운동을 통한 다이어트에 실패하는 이유는 무엇 일까요? 바로 습관을 만들지 못 했기 때문입니다.
                                    <br />
                                    사람들이 운동을 가지 않기 위해 만들어 내는 핑계는 6천 가지가 넘는다고 합니다. <br />
                                    우리는 사람들이 건강한 습관을 만들기 위해 운동을 해야만 하는 ‘이유’를 만들어 주어야 겠다고 생각했습니다.
                                    <br />
                                    <br />
                                    24시 데일리 피트니스 UPFIT은 건강한 습관만들기를 지향합니다.
                                    <br />
                                    전문가들의 체계적인 개별 가이던스와 다양한 G.X 프로그램으로 당신이 운동을 해야만 하는 ‘이유’를 만들어
                                    드립니다.
                                </p>
                            </div>
                        </div>
                        <div>
                            <div />
                            <div>
                                <p className="title">UPDATE YOUR FIT</p>
                                <p style={{ fontWeight: "bold", fontSize: 18 }}>
                                    우리는 멤버들이 운동을 통한 맑은 정신과 건강한 몸을 <br />
                                    매일 ‘재갱신’ 하겠다는 의지를 갖게 해주고 싶었습니다.
                                </p>
                            </div>
                        </div>
                        <div>
                            <div />
                            <div>
                                <p className="title">UPGRADE YOUR LIFE</p>
                                <p style={{ fontWeight: "bold", fontSize: 18 }}>
                                    우리는 멤버들이 하루하루 업데이트되어 가는 자신을 발견하며 <br />
                                    삶의 질을 향상시켜 가도록 하고 싶었습니다.
                                    <br />
                                    <br />
                                    내가 운동을 해야하는 이유, 업핏에서 찾아보세요.
                                </p>
                            </div>
                        </div>
                        <div>
                            <img
                                style={{ width: "100%", height: "100%" }}
                                src={require("../../assets/brand/upfit/upfit-thumbnail.png")}
                                alt=""
                            />
                        </div>
                        <div>
                            <div />
                            <iframe
                                width="100%"
                                height="430px"
                                src="https://www.youtube.com/embed/nP3cBwtWR00"
                                style={{ marginTop: 10, marginBottom: 10 }}
                            />
                        </div>
                    </Desc>
                </Content>
            </>
        );
    }
}



